import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

//Modulos
import { SecurityRoutingModule } from './security.routing.module';
import { UtilsModule } from '../utils/utils.module';
import { SearchModule } from '../search/search.module';
import { ControlsModule } from '../controls/controls.module';
import { GeneralModule } from '../general/general.module';
import { EditorsModule } from '../editors/editors.module';
//Bootstrap
import { TabsModule } from 'ng2-bootstrap/ng2-bootstrap';
//Wijmo
import { WjCoreModule } from 'wijmo/wijmo.angular2.core';
import { WjGridModule } from 'wijmo/wijmo.angular2.grid';
import { WjGridFilterModule } from 'wijmo/wijmo.angular2.grid.filter';
import { WjInputModule } from 'wijmo/wijmo.angular2.input';
//Services
import { EmpresaService } from './empresa';
import { SucursalService } from './sucursal';
import { ProcesoService } from './proceso';
import { UsuarioService } from './usuario';
import { GrupoService } from './grupo';
//Components
import { EmpresasManager } from './empresas.manager';
import { EmpresasEditor } from './empresas.editor';
import { SucursalesManager } from './sucursales.manager';
import { SucursalesEditor } from './sucursales.editor';
import { GruposManager } from './grupos.manager';
import { GruposEditor } from './grupos.editor';

let COMPONENTES = [EmpresasManager, EmpresasEditor, SucursalesManager, SucursalesEditor, GruposManager, GruposEditor];
let SERVICIOS = [EmpresaService, SucursalService, ProcesoService, UsuarioService, GrupoService];

@NgModule({
    imports: [
        FormsModule, ReactiveFormsModule,
        TabsModule.forRoot(),
        SecurityRoutingModule, UtilsModule, SearchModule, EditorsModule, ControlsModule, GeneralModule,
        WjCoreModule, WjInputModule, WjGridModule, WjGridFilterModule
    ],
    declarations: COMPONENTES,
    providers: SERVICIOS,
    exports: COMPONENTES
})
export class SecurityModule {
}